.text {
  &--center {
    text-align: center;
  }
  &--right {
    text-align: right;
  }
  &--p1 {
    font-size: 4rem;
  }
  &--p2 {
    font-size: 2rem;
    line-height: 3rem;
  }
  &--p3 {
    font-size: 1.5rem;
  }
  &--p4 {
    font-size: 1.2rem;
    line-height: 2rem;
  }
  &--p5 {
    font-size: 0.8rem;
  }
  &--bold {
    font-weight: 700;
  }
  &--red {
    color: var(--error);
  }
  &--accent {
    color: var(--accentLight);
  }
  &--warn {
    color: var(--warn);
  }
  &--success {
    color: var(--success);
  }
}
