.song-line {
  display: flex;
  width: 100%;
  padding: 1.8rem;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-radius: 10px;
  cursor: pointer;
  flex-wrap: wrap;
  @include neuro;
  @include MW768 {
    flex-direction: column;
    align-items: flex-start;
  }
  &__top-side,
  &__bottom-side {
    width: 50%;
    display: flex;
    align-items: center;
    margin: 0.6rem 0rem;
  }
  @include MW768 {
    &__top-side,
    &__bottom-side {
      width: 100%;
    }
  }
  &__bottom-side {
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__status {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 1rem;
    background-color: var(--textGrey);
    &--active {
      background-color: var(--success);
    }
  }
  &__title {
    display: flex;
    align-items: center;
  }
  &__hasText {
    font-size: 1.5rem;
    font-weight: 800;
    margin: 0 0.5rem;
  }
  &__buttons {
    display: flex;
    @include MW576 {
      width: 100%;
      margin-top: 1rem;
      .btn {
        width: 100%;
      }
    }
    .btn {
      min-width: 50px;
      &:last-child {
        margin-left: 1rem;
      }
    }
  }
}
