.heading-text {
  &--accent {
    color: var(--accentLight);
  }
  &--center {
    width: 100%;
    text-align: center;
  }
  &--right {
    width: 100%;
    text-align: right;
  }
  &--h1 {
    font-size: 4rem;
    text-transform: uppercase;
    font-weight: 800;
    margin-bottom: 4rem;
    @include MW576 {
      font-size: 2.5rem;
      margin-bottom: 1.8rem;
    }
  }
  &--h2 {
    font-weight: 800;
    font-size: 2rem;
    text-transform: uppercase;
    margin-bottom: 2rem;
    @include MW576 {
      font-size: 1.5rem;
      margin-bottom: 1.5rem;
    }
  }
  &--h3 {
    font-size: 1.4rem;
    margin-bottom: 2rem;
  }
}
