@mixin box-glow($glow-color: var(--accentLight)) {
  background-color: $glow-color;
  box-shadow: 0px 0px 30px $glow-color;
}
@mixin shadow-glow($glow-color: var(--accentLight)) {
  box-shadow: 0px 0px 30px $glow-color;
}
@mixin text-glow {
  text-shadow: 0px 0px 25px var(--accentLight);
}
@mixin neuro {
  background: var(--bgOutpressed)!important;
  box-shadow: 5px 5px 8px var(--boxShadowDark),
    -2px -5px 10px var(--boxShadowLight)!important;
}
@mixin neuro-pressed {
  background: var(--bgPressed)!important;
  box-shadow: inset 0px 15px 20px var(--boxShadowDark),
    inset 0px -5px 30px var(--boxShadowLight), 5px 5px 10px var(--boxShadowDark),
    -5px -5px 10px var(--boxShadowLight)!important;
}
@mixin page-form {
  width: 290px;
  @include absolute-center

}
@mixin absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

}

