.link {
  display: inline-flex;
  &--l2 {
    font-size: 2rem;
  }
  &--l3 {
    font-size: 1.5rem;
  }
  &--l5 {
    font-size: 1rem;
  }
  &--accent {
    color: var(--accentLight);
  }
  &--hover-accent a:hover {
    @include mw1366 {
      color: var(--accentLight);
    }
  }
  &--center {
    text-align: center;
  }
  &:hover {
    @include mw1366 {
      cursor: pointer;
    }
  }
}
