.info-line {
  $saveBtnWidth: 33.3333%;
  width: 100%;
  font-size: 2rem;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
  }
  &__logo-wrapper {
    display: flex;
    align-items: baseline;
    .logo {
      height: 30px;
      width: 30px;
    }
  }
  &__logo {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }
  &__app-name {
    margin-left: 2rem;
    font-weight: bold;
    transition: 0.3s ease-in-out;
    color: var(--accentLight);
    letter-spacing: 2rem;
    @include MW576 {
      margin-left: 1rem;
      letter-spacing: 0.8rem;
    }
    &:hover {
      @include mw1366 {
        cursor: pointer;
        transition: 0.3s linear;
        @include text-glow;
      }
    }
  }
  &__save-update {
    width: $saveBtnWidth;
    .button-type1 {
      max-width: none;
    }
  }
  &__song-title {
    $marginTitle: 0.5rem;
    color: var(--accentLight);
    margin-bottom: 0;
    margin-left: $marginTitle;
    font-size: 2.5rem;
    width: calc(100% - #{$saveBtnWidth} - #{$marginTitle});
    @include MW576 {
      font-size: 1.5rem;
    }
    .divider {
      margin: 0 0.5rem;
    }
    &:hover {
      @include mw1366 {
        cursor: pointer;
      }
    }
  }
  &__offline {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    font-size: 1.5rem;
    color: var(--accentLight);
    text-transform: uppercase;
    .text {
      letter-spacing: 0.2rem;
    }
  }
}
