.snackbar {
  $height: 60px;
  min-height: $height;
  position: absolute;
  top: -100%;
  transition: 0.5s;
  left: 50%;
  transform: translateX(-50%);
  width: 95%;
  z-index: $zIndexMax;
  &__body {
    background-color: var(--accentLight);
    opacity: 1;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 1.5rem 1.5rem;
    border-radius: 10px;
    .text {
      width: 90%;
      word-wrap: break-word;
    }
    &--warn {
      background-color: var(--warn);
    }
    &--success {
      background-color: var(--success);
    }
    &--error {
      background-color: var(--error);
    }
  }
  &.active {
    top: 20px;
  }
}
