// If color variables will change do not forget to change it in store
html {
  --accentLight: #109ef1;
  --secondAccentLight: rgb(253, 255, 122);
  --textGrey: #74797d;
  --error: rgba(231, 91, 91, 0.932);
  --warn: #ff8000;
  --success: rgba(93, 230, 93, 0.863);

  --mainBg: #212529;
  --boxShadowDark: #111315;
  --boxShadowLight: #31373d;
  --bgOutpressed: #212529;
  --bgPressed: linear-gradient(145deg, #1e2125, #23282c);

  &[data-theme='light'] {
    --mainBg: #ecf0f3;
    --boxShadowDark: #c9cccf;
    --boxShadowLight: #ffffff;
    --bgOutpressed: #ecf0f3;
    --bgPressed: linear-gradient(145deg, #d4d8db, #fdffff);
  }
}
