.sign-in-page {
  &__form {
    @include page-form;
  }
  .input:nth-child(3) {
    margin-bottom: 3.5rem;
  }
  &__actions {
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    .link {
      margin-left: 0.5rem;
    }
  }
  &__icon {
    margin-left: 0.5rem;
  }
}
