.menu {
  position: fixed;
  top: 0%;
  transform: translateX(100vw);
  height: 100%;
  background-color: var(--mainBg);
  transition: 0.2s ease-in-out transform;
  &__auth {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__common {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__element {
    text-transform: uppercase;
    margin-bottom: 1rem;
    &:hover {
      @include mw1366 {
        color: var(--accentLight);
        cursor: pointer;
        transition: 0.2s;
        @include text-glow;
      }
    }
  }
  &__avatar {
    width: 100px;
    height: 100px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    input {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      z-index: $zIndexMin;
    }
    img {
      width: 100%;
      height: 100%;
      border: solid var(--mainBg) 5px;
      object-position: center center;
      object-fit: cover;
      border-radius: 50%;
      @include neuro-pressed;
    }
  }
  .icon-user {
    font-size: 8rem;
    width: auto;
  }
  &__app-version {
    font-size: 1.5rem;
    font-weight: 800;
    position: absolute;
    bottom: 5%;
    right: 5%;
  }
  &__theme-switcher {
    margin-top: 1rem;
  }
  &__tuning-fork {
    display: flex;
    select {
      width: 50px;
    }
    img {
      width: 80px;
      height: 80px;
      transform: rotate(25deg);
    }
  }
}
@each $width, $blockWidth in $menuWidthBreakpoints {
  @media screen and (max-width: $width) {
    .menu {
      width: $blockWidth;
    }
    .menu--active {
      transform: translateX(calc(100vw - #{$blockWidth}));
    }
  }
}
