.song-text {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  &__content {
    height: 100%;
  }
  &__controls {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    &-element {
      width: 19%;
      select {
        width: 100%;
      }
      .button-type1 {
        width: 100%;
        max-width: none;
      }
    }
  }
  &__no-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5rem;
  }
  &__text {
    display: flex;
    justify-content: center;
    height: 100%;
    &--small {
      textarea {
        font-size: 1rem;
      }
    }
    &--medium {
      textarea {
        font-size: 1.5rem;
      }
    }
    &--large {
      textarea {
        font-size: 2rem;
      }
    }
  }
  textarea {
    background: var(--mainBg) !important;
    color: var(--textGrey);
    width: 100%;
    height: calc(100% - 40px - 2rem);
    padding: 0.5rem 0rem;
    margin-top: 2rem;
  }
  select {
    margin-left: 0rem;
  }
}
